@font-face {
  font-family: 'BluuNext-Bold';
  font-style: normal;
  font-weight: normal;
  src: url(/assets/fonts/3d1d2f61346290221773a437192c0ad1.woff2) format("woff2"), url(/assets/fonts/9011655e1e03b9649f1dc7e416252183.woff) format("woff"); }

@font-face {
  font-family: 'BluuNext-BoldItalic';
  font-style: normal;
  font-weight: normal;
  src: url(/assets/fonts/08170057eebc23df234c63aa0c2a6b26.woff2) format("woff2"), url(/assets/fonts/0b1317990dfae5f87f13ce62cd99f50a.woff) format("woff"); }

@font-face {
  font-family: 'SteinbeckDemo-Regular';
  font-style: normal;
  font-weight: normal;
  src: url(/assets/fonts/065be483260a49bf0048d32101df9ed3.woff2) format("woff2"), url(/assets/fonts/0d3b04c6f3621da692404ad8e5065347.woff) format("woff"); }

* {
  box-sizing: border-box; }

a.active {
  position: absolute; }

body {
  background: #262525 url(/assets/images/7bf1bc879037f50a0f7239311f0ce603.jpg);
  background-size: 200px 200px;
  color: #f6f5f5; }

.page--text {
  font-family: 'BluuNext-Bold';
  padding: 0 20px; }
  .page--text .headline {
    font-size: 8.87vw;
    line-height: 1;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap; }
  .page--text .subline {
    align-items: center;
    border-bottom: 1px solid #f6f5f5;
    border-top: 1px solid #f6f5f5;
    display: flex;
    justify-content: space-between;
    padding: 20px 0; }
  .page--text .content {
    display: flex;
    font-family: 'SteinbeckDemo-Regular';
    font-size: 1.5vw;
    line-height: 1.4;
    padding: 20px 0; }
  .page--text .current {
    flex: 1 0 50%;
    font-size: 3.2vw;
    line-height: .96;
    text-transform: uppercase; }
  .page--text .about {
    flex: 1 0 50%;
    padding-left: 3vw; }
  .page--text .highlight {
    font-family: 'BluuNext-Bold';
    white-space: nowrap; }
    .page--text .highlight--arrow::before {
      content: '\2192\0020'; }
  .page--text button {
    background: transparent;
    border: 1px solid #f6f5f5;
    border-radius: 32px;
    color: #f6f5f5;
    font-family: 'SteinbeckDemo-Regular';
    font-size: 16px;
    line-height: 30px;
    padding: 10px 30px; }
    .page--text button:hover {
      background: #f6f5f5;
      color: #262525;
      cursor: pointer; }

.canvas__toggle {
  position: absolute; }
